/* Animation and Background Gradient */
@keyframes gradient {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 80px;
}

body {
    margin: 0;
    min-height: 100vh;
    font-family: 'Inter', system-ui, -apple-system, sans-serif;
    background: linear-gradient(-45deg, #579777, #5761cf, #584c83, #3e6993);
    background-size: 300% 300%;
    animation: gradient 5s ease infinite;
    padding: 2rem;
    overflow-x: hidden;
}

/* Reset for consistency */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Sidebar Styles */
.sidebar {
    position: fixed;
    top: 0;
    left: -260px;
    height: 100%;
    width: 260px;
    background-color: #ede5b6;
    border: 4px solid #000;
    border-radius: 0 12px 12px 0;
    box-shadow: 10px 0 0 #000;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    z-index: 1050;
    transition: left 0.3s ease;
}

.sidebar.open {
    left: 0;
}

.sidebar .nav-links {
    list-style: none;
    margin-top: 2rem;
    width: 100%;
}

.sidebar .nav-links li {
    margin: 1rem 0;
}

.sidebar .nav-links .nav-link {
    display: block;
    text-decoration: none;
    background: #ffffff;
    color: #000;
    border: 4px solid #000;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    box-shadow: 4px 4px 0 #000;
    font-weight: 700;
    transition: transform 0.2s;
}

.sidebar .nav-links .nav-link:hover {
    background-color: #111;
    color: #fff;
}

/* Dark Mode Sidebar */
.dark .sidebar {
    background-color: #212121;
    border-color: #000000;
    box-shadow: 10px 0 0 #000000;
}

.dark .sidebar .nav-links .nav-link {
    background: #212121;
    color: #fff;
}

.dark .sidebar .nav-links .nav-link:hover {
    background-color: #1a1a1a;
}

/* Navbar Toggle Button */
.nav-toggle {
    position: fixed;
    top: 1rem;
    left: 1rem;
    background: #ffffff;
    color: #000;
    border: 4px solid #000;
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    box-shadow: 4px 4px 0 #000;
    cursor: pointer;
    z-index: 1100;
    transition: transform 0.2s;
}

.nav-toggle:hover {
    transform: translateY(-4px);
}

/* Responsive Styles */
@media (max-width: 768px) {
    .sidebar {
        width: 100%;
        left: -100%;
        border-radius: 0;
    }

    .sidebar.open {
        left: 0;
    }

    .nav-toggle {
        left: 0.5rem;
    }
}
